.button {
  padding: 27px;
  width: 150px;
  height: 150px;
  background-color: #FFFFFF;
  border-radius: 50%;
  cursor: pointer;
  color: #0B0D17;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 1.25px;
  position: relative;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .button {
    margin-bottom: 90px;
  }
}
@media (min-width: 1440px) {
  .button {
    margin-bottom: -18px;
    margin-right: 5px;
  }
}
.button::before {
  content: "";
  position: absolute;
  width: 246px;
  height: 246px;
  border-radius: 50%;
  top: -48px;
  left: -48px;
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(0.2);
  transition: transform 0.7s;
}
.button:hover::before {
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(1);
}
@media (min-width: 768px) {
  .button {
    width: 242px;
    height: 242px;
    font-size: 32px;
    letter-spacing: 2px;
  }
  .button::before {
    top: 0px;
    left: 0px;
  }
  .button:hover::before {
    width: 418px;
    height: 418px;
    top: -88px;
    left: -88px;
  }
}
@media (min-width: 1440px) {
  .button {
    width: 274px;
    height: 274px;
  }
  .button:hover::before {
    width: 450px;
    height: 450px;
  }
}

