.main-nav__toogle {
  display: block;
  position: absolute;
  width: 19.09px;
  height: 19.09px;
  top: 32px;
  right: 30px;
  z-index: 100;
  border: 0;
  cursor: pointer;
  color: #D0D6F9;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toogle {
    display: none;
  }
}
.main-nav--opened .main-nav__toogle::before, .main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 8px;
  right: 0;
  width: 24px;
  height: 3px;
  background-color: currentColor;
}
.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}
.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}
.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  width: 24px;
  height: 3px;
  background-color: #D0D6F9;
  box-shadow: 0 8px 0 0 #D0D6F9, 0 16px 0 0 #D0D6F9;
  transition: box-shadow 0.5s ease-out;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
    position: relative;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}

